<template>
  <v-hover v-slot="{ hover }">
    <v-card
      :elevation="hover ? 10 : 0"
      :color="(scenario.title) ? scenarioColors[nr] : '#EBEBEB'"
      :to="'/tematy/' + id + '/scenariusze/' + nr"
      class="w-30-ns scenario-card rounded-lg relative "
    >
      <div v-if="pulse" class="pulse-bg bg-mid"></div>
      <div class="card-wrap pa4 lh-solid">
        <div v-if="scenario.title" class="f3 fw7 lh-title tc white--text f-www ph5">{{ scenario.title }}</div>
        <div v-else class="f3 fw7 tc c-light">Scenariusz {{ nr }}</div>
        <div v-if="(selectedFactors.length > 0)" class="tc pt3">
          <v-chip
            v-for="(item, i) in selectedFactors" 
            :key="i" 
            class="mr1 mb1 rounded-lg"
            label
            small
            :class="chipClass(item.type)"
          >
            {{ item.title }}
          </v-chip>
        </div>
      </div>
    </v-card>
  </v-hover>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'ScenarioCard',
  props: ['id', 'nr'],
  data: () => ({
    scenarioColors: {
      '1': '#DB2E89',
      '2': '#A5005C',
      '3': '#FF68B9',
      '4': '#DB2E89'
    }
  }),
  computed: {
    ...mapGetters('scenarios', ['scenarioByNr', 'senarioFactors', 'loading']),
    scenario() {
      return this.scenarioByNr(this.nr)
    },
    selectedFactors() {
      return this.senarioFactors(this.nr)
    },
    pulse() {
      if (this.nr == 1 && !this.scenario.title) {
        return true
      } else if (this.nr == 2 && !this.scenario.title && this.scenarioByNr(1).title) {
        return true
      } else if (this.nr == 3 && !this.scenario.title && this.scenarioByNr(1).title && this.scenarioByNr(2).title) {
        return true
      } else if (this.nr == 4 && !this.scenario.title && this.scenarioByNr(1).title && this.scenarioByNr(2).title && this.scenarioByNr(3).title) {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    chipClass(type) {
      return {
        'chip1': type == 1,
        'chip2': type == 2,
        'chip3': type == 3,
        'chip4': type == 4,
        'chip5': type == 5,
        'chip6': type == 6,
        'chip-custom': type > 6
      }
    },
  }
}
</script>

<style>
.scenario-card {
  will-change: box-shadow;
  transition-duration: 0.28s;
  transition-property: box-shadow, transform, opacity;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.card-wrap {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  transform: translateY(-50%);
}
</style>