<template>
  <section v-if="this.loading" class="tc page-loader">
    <v-progress-circular
      :size="366"
      :width="8"
      color="#db2f89"
      indeterminate
    ></v-progress-circular>
  </section>
  <div v-else class="w-100" :class="{ 'to-print': toPrint, 'to-read': toRead }">
    <section>
      <div class="section-wrap center ph3 w-100 w-90-l tc">
        <h1 class="c-dark fw7 mt6 mb5 f3 f2-m f1-l w-100 w-70-l lh-solid center">{{ title }}</h1>
      </div>
    </section>

    <section>
      <div class="section-wrap center ph3 w-100 w-90-l f7 c-dark">

        <div class="w-100 tc pt2 pb4">
          Wysokie oddziaływanie,<br />tendencja wzrostowa
        </div>
        
        <div class="y-axis w-100">
          <div class="w-100 flex flex-wrap min-h-scenario justify-center">
            <div class="w-16-ns"></div>
            <scenario-card :id="id" nr="1" />
            <div class="w-axis arr-up relative">
              <div class="y-label">
                {{ keyFactors[1].title }}
              </div>
            </div>
            <scenario-card :id="id" nr="2" />
            <div class="w-16-ns"></div>
          </div>

          <div class="w-100 flex flex-wrap h-axis x-axis">
            <div class="w-16-ns flex items-center justify-center">
              <div class="tc">Niskie oddziaływanie,<br />tendencja spadkowa</div>
            </div>
            <div class="w-30-ns flex items-end justify-center x-label">
              {{ keyFactors[0].title }}
            </div>
            <div class="w-axis">
              
            </div>
            <div class="w-30-ns arr-right relative flex items-start justify-center x-label">
              {{ keyFactors[0].title }}
            </div>
            <div class="w-16-ns flex items-center justify-center">
              <div class="tc">Wysokie oddziaływanie,<br />tendencja wzrostowa</div>
            </div>
          </div>


          <div class="w-100 flex flex-wrap min-h-scenario justify-center">
            <div class="w-16-ns"></div>
            <scenario-card :id="id" nr="3" />
            <div class="w-axis relative">
              <div class="y-label label-bottom flex items-end justify-center f7">
                {{ keyFactors[1].title }}
              </div>
            </div>
            <scenario-card :id="id" nr="4" />
            <div class="w-16-ns"></div>
          </div>
        </div>

        <div class="w-100 tc pv4">
          Niskie oddziaływanie,<br />tendencja spadkowa
        </div>

      </div>
    </section>
    <v-fab-transition>
      <v-btn
        v-model="fab"
        color="primary"
        dark
        fab
        large
        bottom
        right
        fixed
        @click="printPdf"
        v-show="showFab"
      >
        <v-icon>
          mdi-printer
        </v-icon>
      </v-btn>
    </v-fab-transition>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import ScenarioCard from '@/components/ScenarioCard.vue';
import html2canvas from 'html2canvas';
import { jsPDF } from 'jspdf';
export default {
  props: ['id'],
  components: {
    ScenarioCard
  },
  data: () => ({
    currentStep: 5,
    fab: false,
    showFab: false,
    toPrint: false,
  }),
  computed: {
    ...mapGetters('scenarios', ['factorsByType', 'keyFactors', 'title', 'steps', 'editable', 'loading']),
    toRead() {
      return (this.editable) ? false : true;
    }
  },
  created() {
    this.setCurrentSlide(1);
  },
  mounted() {
    setTimeout(() => {
        this.showFab = true;
      }, 100);
  },
  methods: {
    ...mapMutations('scenarios', ['setCurrentSlide','setEditable']),
    printPdf() {
      var editableTmp = this.editable;
      this.toPrint = true;
      this.setEditable(false);
      
      var printArea = document.getElementById('app');

      setTimeout(() => {
        html2canvas(printArea).then(function(canvas) {
          
          var imgData = canvas.toDataURL('image/png');

          var doc = new jsPDF('p', 'mm', [297, 210]);

          const imgProps = doc.getImageProperties(imgData);
          const pdfWidth = doc.internal.pageSize.getWidth();
          const pdfHeight = (imgProps.height * pdfWidth) / imgProps.width;
          doc.addImage(imgData, 'PNG', 0, 0, pdfWidth, pdfHeight);
          
          doc.save('schemat.pdf');
        });

        this.toPrint = false;
        this.setEditable(editableTmp);
      }, 50);
    }
  }
}
</script>

<style>
.w-16-ns {
  width: 16%;
}
.w-30-ns {
  width: 30%;
}
.w-axis {
  width: 72px; /*6%;*/
}
.h-axis {
  height: 76px;
}
.min-h-scenario {
  min-height: 230px;
}
.y-axis, .x-axis {
  position: relative;
}
.y-axis:before {
  content: '';
  display: block;
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  background-color: #C9C9C9;
  width: 6px;
  transform: translateX(-50%);
}
.x-axis:before {
  content: '';
  display: block;
  position: absolute;
  left: 17%;
  top: 50%;
  right: 17%;
  background-color: #C9C9C9;
  height: 6px;
  transform: translateY(-50%);
}
.arr-up:before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 24px solid #C9C9C9;
  position: absolute;
  top: -10px;
  left: 50%;
  transform: translateX(-50%);
}
.arr-right:before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 10px 0 10px 24px;
  border-color: transparent transparent transparent #C9C9C9;
  position: absolute;
  top: 50%;
  right: -10px;
  transform: translateY(-50%);
}
.x-label {
  padding: 6px;
  line-height: 32px;
}
.y-label {
  position: absolute;
  transform: rotate(270deg);
  transform-origin: top left;
  z-index: 3;
  height: 64px;
  width: 220px;
  left: 4px;
  top: 100%;
  text-align: center;
  overflow: hidden;
  line-height: 32px;
}
.y-label.label-bottom {
  max-width: none;
}
</style>