import scenarios from '../../api/scenarios'

const state = () => ({
  id: {},
  //all: {},
  one: {
    id: 1,
    title: '',
    factors_types: {},
    topics_factors_types: [],
    factors: [],
    scenarios: [],
    editable: 0
  },
  steps: [
    {
      title: 'Budowa scenariusza',
      subtitle: '',
      content: 'Wprowadź czynniki mające wpływ na badane zagadnienie i wybierz spośród nich dwa kluczowe, które w największym stopniu wpływają na perspektywy jego rozwoju.',
      color: ''
    },
    {
      title: 'Nazwij scenariusze',
      subtitle: '',
      content: 'Pomyśl o kreatywnych nazwach dla czterech scenariuszy. Osie układu odzwierciedlają siłę z jaką oddziałują czynniki kluczowe.',
      color: ''
    },
    {
      title: 'Opisz scenariusz',
      subtitle: '',
      content: 'Wprowadź nazwę scenariusza. Dodaj czynniki, które w największym stopniu wpływają na rozwój tego scenariusza.',
      color: ''
    }
  ],
  currentStep: 0,
  currentSlide: 0,
  loading: false,
})

const getters = {
  id(state) {
    return state.id
  },
  factorsTypes: (state) => {
    return state.one.factors_types
  },
  factorsByType: (state) => (type) => {
    return (state.one.factors) ? state.one.factors.filter((el) => el.type == type) : []
  },
  factorsByTypeLength: (state, getters) => (type) => {
    return (getters.factorsByType(type)) ? getters.factorsByType(type).length : 0
  },
  topicType: (state) => (nr) => {
    return (state.one.topics_factors_types[nr]) ? state.one.topics_factors_types[nr] : {}
  },
  topicsFactorsTypes: (state) => {
    return state.one.topics_factors_types
  },
  topicsFactorsTypesLength: (state, getters) => {
    return getters.topicsFactorsTypes.length
  },
  keyFactors: (state) => {
    return (state.one.factors) ? state.one.factors.filter((el) => el.key_factor == '1') : []
  },
  notKeyFactors: (state) => {
    return state.one.factors.filter((el) => el.type == '1' && el.key_factor != '1') || []
  },
  /*findFactorsMultiple: (state) => (ids) => {
    return state.one.factors.filter(function(item) {
      return ids.indexOf(item.id) != -1;
    });
  },*/
  factors: (state, getters) => {
    return getters.factorsByType(1)
  },
  factorsLength: (state, getters) => {
    return getters.factors.length
  },
  keyFactorsLength: (state, getters) => {
    return getters.keyFactors.length
  },
  editable(state) {
    return (state.one.editable == '1') ? true : false
  },
  loading(state) {
    return state.loading
  },
  steps(state) {
    return state.steps
  },
  title(state) {
    return state.one.title
  },
  currentSlide(state) {
    return state.currentSlide
  },
  scenarioByNr: (state) => (nr) => {
    return state.one.scenarios.find(el => el.ord == nr) || {}
  },
  senarioFactors: (state) => (nr) => {
    return state.one.factors.filter((el) => el['scenario_' + nr] == 1)
  },
}

const mutations = {
  setId: (state, id) => {
    state.id = id
  },
  insertFactor: (state, data) => {
    state.one.factors.push(data)
  },
  setAll: (state, data) => {
    state.all = data
  },
  setOne: (state, data) => {
    state.one = data
  },
  setLoading: (state, data) => {
    state.loading = data
  },
  setListener: (state, interval) => {
    state.listener = interval
  },
  setCurrentSlide: (state, slide) => {
    state.currentSlide = slide;
  },
  setEditable(state, data) {
    state.one.editable = data
  },
}

const actions = {
  getTopic({commit}, id) {
    commit('setLoading', true);
    return scenarios.getTopic(id)
    .then(data => {
      commit('setOne', data);
      commit('setLoading', false);
      return data;
    })
  },
  updateFactor({commit, dispatch}, factor) {
    return scenarios.updateFactor(factor)
    .then(data => {
      commit('setOne', data);
      dispatch('setup');
      return data;
    })
  },
  deleteFactor({commit, dispatch}, factor) {
    return scenarios.deleteFactor(factor)
    .then(data => {
      commit('setOne', data);
      dispatch('setup');
      return data;
    })
  },
  deleteTopicType({commit, dispatch}, params) {
    return scenarios.deleteTopicType(params)
    .then(data => {
      if (!data.error) {
        commit('setOne', data);
        dispatch('setup');
      }
      return data;
    })
  },
  createAddTopicType({commit, dispatch}, params) {
    return scenarios.createAddTopicType(params)
    .then(data => {
      if (!data.error) {
        commit('setOne', data);
        dispatch('setup');
      }
      return data;
    })
  },
  updateKeyFactors({commit, dispatch}, params) {
    return scenarios.updateKeyFactors(params)
    .then(data => {
      commit('setOne', data);
      dispatch('setup');
      return data;
    })
  },
  updateScenario({commit}, params) {
    return scenarios.updateScenario(params)
    .then(data => {
      commit('setOne', data);
      return data;
    })
  },
  updateScenarioFactors({commit}, params) {
    return scenarios.updateScenarioFactors(params)
    .then(data => {
      commit('setOne', data);
      return data;
    })
  },
  setup() {
    /*var step = getters.notTakenStep;
    if (!getters.editable) {
      if (step > 1) { 
        step = 4;
      }
    }
    commit('setCurrentStep', step);
    commit('setCurrentSlide', step);*/
  },
  addTopicType({commit}, params) {
    return scenarios.addTopicType(params)
    .then(data => {
      commit('setOne', data);
      return data;
    })
  }
  /*
  startListener({commit, state, dispatch}) {
    if (state.listener === undefined) {
      commit('setListener', setInterval(() => {
        dispatch('getAll')
      }, 60000))
    }
  },
  update({state, commit}, {paramName, paramValue}) {
    commit('setLoading', true);
    return parameters.update(state.currentDevice, paramName, paramValue)
    .then(data => {
      //if (!data.error) { dispatch('getAll'); }
      commit('setLoading', false);
      return data;
    })
  },*/
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}