<template>
  <div class="w-100 overflow-hidden">
    <section v-if="this.loading" class="tc page-loader">
      <v-progress-circular
        :size="366"
        :width="8"
        color="#db2f89"
        indeterminate
      ></v-progress-circular>
    </section>
    <section v-else class="diagram" :class="{ 'to-print': toPrint, 'to-read': toRead }" id="print-canvas">
      <div class="section-wrap center ph3 w-100 w-90-l pb4">
        <h1 class="tc c-dark fw7 mv6 f3 f2-m f1-l w-100 w-70-l lh-solid center">{{ title }}</h1>
        
        <!-- czynniki kluczowe / sily napedowe -->
        <div class="flex flex-wrap w-100">

          <div class="w-50 flex items-end">
            <div class="w-100 flex flex-wrap">
          
              <div class="w-third flex flex-column justify-center di-le">
                <div class="chip-wrap" v-for="(item, i) in keyFactors" :key="i" :class="[nodeClass(i, keyFactorsLength)]">
                  <v-chip
                    label
                    link
                    color="#db2f89"
                    text-color="white"
                    class="tc rounded-lg"
                  >{{ item.title }}</v-chip>
                </div>
              </div>
              <div class="w-2"></div>
              <div class="w-third flex items-end">
                <div class="w-100 pv7 relative z-1" :class="{ 'di-step-2': keyFactorsLength > 0 }">
                  <div class="way-tl"></div>
                  <v-dialog
                    v-if="editable"
                    v-model="dialogKey"
                    scrollable
                    persistent
                    max-width="600px"
                  >
                    <template v-slot:activator="{ on: dialogKey }">
                      <div class="relative btc">
                      <div v-if="keyFactorsLength < 2 && factorsLength > 1" class="pulse-bg chip2"></div>
                        <v-tooltip top max-width="400" color="#db2f89">
                        <template v-slot:activator="{ on: tooltip }">
                          <v-hover v-slot="{ hover }">
                            <v-btn block height="64" 
                              :elevation="hover ? 8 : 0" 
                              :outlined="(factorsLength < 2) ? true : false"
                              color="#db2f89"
                              :class="{'white--text': factorsLength > 1 }"
                              :disabled="factorsLength < 2"
                              v-on="{...tooltip, ...dialogKey}"
                              class="rounded-lg z-3"
                            >
                              Czynniki kluczowe
                            </v-btn>
                          </v-hover>
                        </template>
                        <span>{{ factorsTypes2.content }}</span>
                        </v-tooltip>
                      </div>
                    </template>
                    <v-card>
                      <v-card-title>Wybierz 2 czynniki kluczowe</v-card-title>
                      <v-divider></v-divider>
                      
                      <v-card-text class="mt3">
                        <v-chip-group
                          v-model="keyFactorsSelection"
                          column
                          multiple
                          active-class="chip2"
                        >
                          <v-chip
                            v-for="(item, i) in factors" 
                            :key="i" 
                            filter
                            label
                            class="rounded-lg"
                            :value="item.id"
                            :disabled="keyFactorsSelectionLength >= 2 && !keyFactorsSelection.includes(item.id)"
                          >
                            {{ item.title }}
                          </v-chip>
                        </v-chip-group>
                      </v-card-text>

                      <v-divider></v-divider>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn
                          text
                          @click="dialogKey = false"
                        >
                          Anuluj
                        </v-btn>
                        <v-btn
                          text
                          @click="saveKeyFactors"
                        >
                          Zapisz
                        </v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                  <div v-else class="relative btc">
                    <div v-if="currentSlide == 1" class="pulse-bg chip2"></div>
                    <v-tooltip top max-width="400" color="#db2f89">
                    <template v-slot:activator="{ on, attrs }">
                    <v-hover v-slot="{ hover }">
                      <v-btn block height="64" 
                        :elevation="hover ? 8 : 0" 
                        :outlined="(factorsLength < 2) ? true : false"
                        color="#db2f89"
                        :class="{'white--text': factorsLength > 1 }"
                        :disabled="factorsLength < 2"
                        v-bind="attrs"
                        v-on="on"
                        class="rounded-lg z-3"
                      >
                        Czynniki kluczowe
                      </v-btn>
                    </v-hover>
                    </template>
                    <span>{{ factorsTypes2.content }}</span>
                    </v-tooltip>
                  </div>
                  <div class="triangle-down"></div>
                </div>
              </div>
              <div class="w-2"></div>

            </div>
          </div>

          <div class="w-50">
            <div class="w-100 flex flex-wrap">
              <div class="w-2"></div>
              <div class="w-third flex items-end">
                <div class="w-100 pv7 relative z-1" :class="{ 'di-step-3': topicsFactorsTypes[1] && factorsByTypeLength(topicsFactorsTypes[1].id_factor_type) > 0 }">
                  <div class="way-tr"></div>
                  <diagram-button :nr="1" v-on:add-factor="addFactor" v-on:del-type="confirmDelType" v-on:create-new-type="createNewType" />
                  <div class="triangle-down"></div>
                </div>
              </div>
              <div class="w-2"></div>
              <div v-if="topicsFactorsTypes[1]" class="w-third flex flex-column justify-center di-r">
                <div class="chip-wrap" v-for="(item, i) in factorsByType(topicsFactorsTypes[1].id_factor_type)" :key="i" :class="[nodeClass(i, factorsByTypeLength(topicsFactorsTypes[1].id_factor_type))]">
                  <v-chip
                    label
                    link
                    :color="factorsTypes[topicsFactorsTypes[1].id_factor_type].color"
                    text-color="white"
                    class="tc rounded-lg"
                    @click.stop="(editable) ? editFactor(item) : null"
                  >{{ item.title }}</v-chip>
                </div>
              </div>
              <div v-else class="w-third flex di-r"></div>
            </div>
          </div>
        </div>

        <!-- scenariusze -->
        <div class="flex flex-wrap w-100">
          <div class="w-2"></div>
          <div class="w-1"></div>
          <div class="w-2 relative"><div class="way-lmid"></div></div>
          <div class="w-2 relative di-step-5">
            <div class="relative">
              <div v-if="keyFactorsLength > 1" class="pulse-bg chip4"></div>
              <v-hover v-slot="{ hover }">
                <v-btn block height="64"
                  :elevation="hover ? 8 : 0" 
                  :outlined="(keyFactorsLength > 1) ? false : true"
                  color="#231f20"
                  :class="{'white--text': keyFactorsLength > 1 }"
                  :disabled="keyFactorsLength < 2"
                  @click="goToScenarios"
                  class="rounded-lg z-3"
                >
                  Scenariusze
                </v-btn>
              </v-hover>
            </div>
          </div>
          <div class="w-2 relative"><div class="way-rmid"></div></div>
          <div class="w-1"></div>
          <div class="w-2"></div>
        </div>

        <!-- 0 / 2 -->
        <div class="flex flex-wrap w-100">
          <div class="w-50 flexi">
            <div class="w-100 flex flex-wrap">
            
              <div class="w-third flex flex-column justify-center di-le relative z-1">
                <div class="chip-wrap" v-for="(item, i) in factors" :key="i" :class="[nodeClass(i, factorsLength)]">
                  <v-chip
                    label
                    link
                    :color="factorsTypes[topicsFactorsTypes[0].id_factor_type].color"
                    text-color="white"
                    @click.stop="(editable) ? editFactor(item) : null"
                    class="tc rounded-lg"
                  >{{ item.title }}</v-chip>
                </div>
              </div>

              <div class="w-2"></div>
              <div class="w-third flex items-start">
                <div class="w-100 pv7 relative pulse-container" :class="{ 'di-step-1': factorsLength > 0 }">
                  <div class="way-start"></div>
                  <diagram-button :nr="0" v-on:add-factor="addFactor" v-on:del-type="confirmDelType" v-on:create-new-type="createNewType" />
                  <div class="triangle-up"></div>
                </div>
              </div>

              <div class="w-2"></div>
            </div>
          </div>

          <div class="w-50 flexi">
            <div class="w-100 flex flex-wrap" :class="{ 'not-last': notLast(2), 'next-has-factors': nextHasFactors(2) }">
              <div class="w-2 relative"><div class="way-bl"></div><div class="way-triangle"></div></div>

              <div class="w-third flex items-start relative">
                <div class="way-br"></div>
                <div class="step-extra"></div>
                <div class="w-100 pv7 relative" :class="{ 'di-step-4': hasFactors(2) }">
                  <diagram-button :nr="2" v-on:add-factor="addFactor" v-on:del-type="confirmDelType" v-on:create-new-type="createNewType" />
                  <div class="triangle-up"></div>
                </div>
              </div>

              <div class="w-2"></div>
              
              <div v-if="topicsFactorsTypes[2]" class="w-third flex flex-column justify-center di-r">
                <div class="chip-wrap" v-for="(item, i) in factorsByType(topicsFactorsTypes[2].id_factor_type)" :key="i" :class="[nodeClass(i, factorsByTypeLength(topicsFactorsTypes[2].id_factor_type))]">
                  <v-chip
                    label
                    link
                    :color="factorsTypes[topicsFactorsTypes[2].id_factor_type].color"
                    text-color="white"
                    class="tc rounded-lg"
                    @click.stop="(editable) ? editFactor(item) : null"
                  >{{ item.title }}</v-chip>
                </div>
              </div>
              <div v-else class="w-third flex di-r"></div>
            </div>

            
            <!--extra elements-->
            <div v-for="(nrEx, i) in extraSections" :key="i" class="w-100 flex flex-wrap" :class="{ 'not-last': notLast(nrEx), 'next-has-factors': nextHasFactors(nrEx) }">
              <div class="w-2 relative"><div class="way-bl"></div><!--<div class="way-triangle"></div>--></div>

              <div class="w-third flex items-start relative">
                <div class="way-br"></div>
                <div class="step-extra"></div>
                <div class="w-100 pv6 relative" :class="{ 'di-step-4': hasFactors(nrEx) }">
                  <diagram-button :nr="nrEx" v-on:add-factor="addFactor" v-on:del-type="confirmDelType" v-on:create-new-type="createNewType" />
                  <!--<div class="triangle-up"></div>-->
                </div>
              </div>

              <div class="w-2"></div>
              
              <div v-if="topicsFactorsTypes[nrEx]" class="w-third flex flex-column justify-center di-r">
                <div class="chip-wrap" v-for="(item, i) in factorsByType(topicsFactorsTypes[nrEx].id_factor_type)" :key="i" :class="[nodeClass(i, factorsByTypeLength(topicsFactorsTypes[nrEx].id_factor_type))]">
                  <v-chip
                    label
                    link
                    :color="factorsTypes[topicsFactorsTypes[nrEx].id_factor_type].color"
                    text-color="white"
                    class="tc rounded-lg"
                    @click.stop="(editable) ? editFactor(item) : null"
                  >{{ item.title }}</v-chip>
                </div>
              </div>
              <div v-else class="w-third flex di-r"></div>

            </div>



          </div>

        </div>

        <!--tu bylo extra kiedys-->

      </div>
    </section>

    <!--edit factor-->
    <v-dialog
      v-model="dialog"
      persistent
      max-width="360"
    >
      <v-card v-if="factorsTypes[editedFactor.type]" :color="(editedFactor.type) ? factorsTypes[editedFactor.type].color : null">
        <v-card-title class="headline"></v-card-title>
        <v-card-text>
          <v-textarea
            v-model="editedFactor.title"
            name="input-7-1"
            :label="(editedFactor.label) ? editedFactor.label : null"
            rows="3"
            hide-details
            dark
            ref="addfactor"
            maxlength="64"
          ></v-textarea>
        </v-card-text>

        <v-card-actions>
          <v-menu
            v-if="editedFactor.id"
            transition="slide-y-transition"
            bottom
            offset-y
            rounded="lg"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="white"
                text
                v-bind="attrs"
                v-on="on"
              >
                Usuń
              </v-btn>
            </template>
            <v-list dark color="#db2f89">
              <v-list-item @click="delFactor">
                <v-list-item-title>Potwierdzam usunięcie</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
          <v-spacer></v-spacer>
          <v-btn
            color="white"
            text
            @click="dialog = false"
          >
            Anuluj
          </v-btn>
          <v-btn
            color="white"
            text
            @click="insertFactor"
          >
            Zapisz
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!--add type-->
    <v-dialog
      v-model="dialogNewType"
      persistent
      max-width="360"
    >
      <v-card color="#18b9a7">
        <v-card-title class="headline"></v-card-title>
        <v-card-text>
          <v-form
            ref="formtype"
            v-model="validType"
            lazy-validation
            class="pt3"
          >
            <v-text-field
              type="text"
              v-model="newTypeTitle"
              ref="newtype"
              label="Nowa grupa czynników"
              dark
              maxlength="20"
              :rules="typeTitleRules"
            ></v-text-field>
          </v-form>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="white"
            text
            @click="dialogNewType = false"
          >
            Anuluj
          </v-btn>
          <v-btn
            color="white"
            text
            @click="insertNewType"
            :disabled="!validType"
            :loading="loadingCreateType"
          >
            Zapisz
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!--confirm del type-->
    <v-dialog
      v-model="dialogDelType"
      persistent
      max-width="320"
      dark
    >
      <v-card color="#db2f89">
        <v-card-title v-if="editedType.id_factor_type" class="headline">
          Na pewno chcesz usunąć {{ (factorsTypes[editedType.id_factor_type]) ? factorsTypes[editedType.id_factor_type].title : 'grupę' }}?
        </v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            text
            @click="dialogDelType = false"
          >
            Anuluj
          </v-btn>
          <v-btn
            text
            @click="delType"
            :loading="loadingDelType"
            :disabled="loadingDelType"
          >
            Usuń
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!--maks elemetow-->
    <v-dialog
      v-model="dialogMax"
      persistent
      max-width="320"
      dark
    >
      <v-card color="#db2f89">
        <v-card-title class="headline">
          Osiągnąłeś maksymalną ilość elementów.
        </v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            text
            @click="dialogMax = false"
          >
            Zamknij
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-fab-transition>
      <v-btn
        v-model="fab"
        color="primary"
        dark
        fab
        large
        bottom
        right
        fixed
        @click="printPdf"
        v-show="showFab"
      >
        <v-icon>
          mdi-printer
        </v-icon>
      </v-btn>
    </v-fab-transition>
  </div>
</template>

<script>
// @ is an alias to /src
import DiagramButton from '@/components/DiagramButton.vue'
import { mapGetters, mapActions, mapMutations } from 'vuex'
import html2canvas from 'html2canvas';
import { jsPDF } from 'jspdf';

export default {
    components: {
      DiagramButton
    },
    data: () => ({
      dialogNewType: false,
      newTypeTitle: '',
      typeTitleRules: [
        v => !!v || 'Nadaj nazwę nowej grupie'
      ],
      validType: true,
      loadingCreateType: false,
      dialog: false,
      dialogKey: false,
      dialogDelType: false,
      editedType: {},
      keyFactorsSelection: [],
      dialogm1: '',
      editedFactor: {
        id: '',
        id_topic: '',
        title: '',
        ord: '',
        type: ''
      },
      loadingDelType: false,
      factorsTypes2: {
        content: 'Czynniki zmian w otoczeniu zewnętrznym i wewnętrznym badanego obszaru, które w największym stopniu wpłyną na perspektywy jego rozwoju. Są to ważne zagadnienia, stanowiące wyzwania wymagające podjęcia odpowiednich działań, które mają istotny wpływ na rozwiązanie problemów wynikających z oceny aktualnego stanu.'
      },
      dialogMax: false,
      fab: false,
      showFab: false,
      toPrint: false,
    }),
    computed: {
      ...mapGetters('scenarios', ['id','topicsFactorsTypes','topicsFactorsTypesLength','factorsByType','factorsByTypeLength','keyFactorsLength','factorsTypes',  'currentSlide','factors', 'factorsLength', 'keyFactors', 'title', 'steps', 'editable', 'loading']),
      keyFactorsSelectionLength() {
        return this.keyFactorsSelection.length
      },
      availableTypes() {
        return this.factorsTypes
      },
      extraSections() {
        var extra = (this.topicsFactorsTypesLength > 3) ? Object.keys(this.topicsFactorsTypes).slice(3) : [];

        if (this.topicsFactorsTypesLength > 2 && this.editable && this.topicsFactorsTypesLength < 5) {
          extra.push(this.topicsFactorsTypesLength);
        }
        return extra;
      },
      toRead() {
        return (this.editable) ? false : true;
      }
    },
    watch: {
      dialogKey(opened) {
        if (opened) {
          this.keyFactorsSelection = this.keyFactors.map(el => el.id);
        }
      }
    },
    mounted() {
      setTimeout(() => {
          this.showFab = true;
        }, 100);
    },
    methods: {
      ...mapActions('scenarios', ['getTopic','updateFactor','updateKeyFactors','deleteFactor','deleteTopicType','createAddTopicType']),
      ...mapMutations('scenarios', ['setCurrentSlide','setEditable']),
      nodeClass(i, all) {
        if (i == 0 && all == 1) {
          return { 'di-single': true }
        } else if (i == 0) {
          return { 'di-first': true }
        } else if (i == all - 1) {
          return { 'di-last': true }
        } else {
          return { 'di-mid': true }
        }
        
      },
      addFactor(type) {
        if ((type == 1 && this.factorsByTypeLength(type) < 15) || this.factorsByTypeLength(type) < 10) {
          this.editedFactor = {
            id: '',
            id_topic: this.id,
            title: '',
            type: type,
            key_factor: '0',
            ord: '0',
            label: 'Dodaj ' + this.factorsTypes[type].title
          }
          this.dialog = true;
          setTimeout(() => {
            this.$refs.addfactor.focus()
          }, 150);
        } else {
          this.dialogMax = true;
        }
      },
      editFactor(factor) {
        var label = 'Czynnik';
        this.editedFactor = {...factor};
        this.editedFactor.label = label;
        this.dialog = true;
      },
      insertFactor() {
        this.updateFactor(this.editedFactor)
        .then(() => this.dialog = false );
      },
      delFactor() {
        this.deleteFactor(this.editedFactor)
        .then(() => this.dialog = false );
      },
      confirmDelType(topicFactorType) {
        this.editedType = {...topicFactorType};
        this.dialogDelType = true;
      },
      delType() {
        this.loadingDelType = true;
        this.deleteTopicType(this.editedType)
        .then(() => {
          this.loadingDelType = false;
          this.dialogDelType = false;
        })
      },
      createNewType() {
        this.dialogNewType = true;
        setTimeout(() => {
          this.$refs.formtype.reset();
          //this.$nextTick(() => this.$refs.newtype.focus());
          this.$refs.newtype.focus()
        }, 150);
      },
      insertNewType() {
        this.$refs.formtype.validate();
        this.loadingCreateType = true;
        this.createAddTopicType({
          id_topic: this.id,
          title: this.newTypeTitle
        })
        .then(() => {
          this.loadingCreateType = false;
          this.dialogNewType = false;
        });
      },
      saveKeyFactors() {
        //keyFactorsSelection
        //let selectedIds = this.keyFactorsSelection.map(el => el.id);
        
        this.updateKeyFactors({
          id_topic: this.id,
          keyFactorsId: this.keyFactorsSelection //.map(el => el.id)
        });
        
        this.dialogKey = false;
      },
      goToScenarios() {
        this.$router.push('/tematy/' + this.id + '/scenariusze');
      },
      hasFactors(nr) {
        if (this.topicsFactorsTypes[nr]) {
          return (this.factorsByTypeLength(this.topicsFactorsTypes[nr].id_factor_type) > 0) ? true : false
        } else {
          return false
        }
      },
      nextHasFactors(nr) {
        return this.hasFactors(parseInt(nr) + 1)
      },
      notLast(nr) {
        return (this.topicsFactorsTypesLength > nr + 1 || (this.editable && this.topicsFactorsTypesLength > nr && nr < 4)) ? true : false
      },
      /*printImage() {
        var editableTmp = this.editable;
        this.toPrint = true;
        this.setEditable(false);
        
        var printArea = document.getElementById('app');
        //var printArea = document.body;
        setTimeout(() => {
          html2canvas(printArea).then(function(canvas) {
              printArea.appendChild(canvas);
          });
          this.toPrint = false;
          this.setEditable(editableTmp);
        }, 50);
      },*/
      printPdf() {
        var editableTmp = this.editable;
        this.toPrint = true;
        this.setEditable(false);
        
        var printArea = document.getElementById('app');

        setTimeout(() => {
          html2canvas(printArea).then(function(canvas) {
            
            var imgData = canvas.toDataURL('image/png');

            var doc = new jsPDF('p', 'mm', [297, 210]);

            const imgProps = doc.getImageProperties(imgData);
            const pdfWidth = doc.internal.pageSize.getWidth();
            const pdfHeight = (imgProps.height * pdfWidth) / imgProps.width;
            doc.addImage(imgData, 'PNG', 0, 0, pdfWidth, pdfHeight);
            
            doc.save('schemat.pdf');
          });

          this.toPrint = false;
          this.setEditable(editableTmp);
        }, 50);
      }
    }
  }
</script>

<style>
.btc {
  min-height: 64px;
  width: 100%;
}
.chip-wrap .v-chip--clickable {
  will-change: box-shadow;
  transition-duration: 0.28s;
  transition-property: box-shadow, transform, opacity;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.chip-wrap .v-chip--clickable:hover {
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12) !important;
}
/*pulse*/
.pulse-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 15px;
  z-index: 1;
}
.pulse-ico {
  position: absolute;
  top: 0;
  left: calc(50% - 32px);
  width: 56px;
  height: 56px;
  border-radius: 28px;
  z-index: 1;
  background-color: #c9c9c9;
  transform: translateX(-50%);
}
.pulse-bg, .pulse-ico {
  animation: pulse 1.2s ease infinite;
  /*background: #ffd332;*/
}
@keyframes pulse {
  0% {
    transform: scale(1, 1);
  }
