<template>
  <div class="w-100">
    <section class="section-wrap center ph3 w-100 w-90-l mt6 mb6">
      <h1 v-if="!editable" class="tc c-dark f2 f1-l">{{ (scenarioTitle) ? scenarioTitle : 'Scenariusz ' + nr }}</h1>
      <input v-else type="text" v-model="scenarioTitle" @blur="saveScenario" placeholder="Nazwa scenariusza" class="w-100 tc f2 f1-l discrete c-dark fw7" />
    </section>
    <section class="section-wrap center ph3 w-100 w-90-l mb3 flex justify-center">
      <div class="ph3 w-50">
        <h5 class="c-dark mb4 fw7 f5">Czynniki kluczowe</h5>
        <div class="flex">
          <div class="pr2">
            <v-chip
              class="mb3 rounded-lg chip2 h36"
              label
            >
              {{ keyFactors[0].title }}
            </v-chip>
            <div class="mt2 f5" v-html="axisLegend.x"></div>
          </div>
          <div class="pl2">
            <v-chip
              class="mb3 rounded-lg chip2 h36"
              label
            >
              {{ keyFactors[1].title }}
            </v-chip>
            <div class="mt2 f5" v-html="axisLegend.y"></div>
          </div>
        </div>
      </div>
      <div class="ph3">
        <h5 v-if="(selectedFactors.length > 0)" class="c-dark mb4 fw7 f5">Dzikie karty, technologie</h5>
        <div v-else class="w-100 h2"></div>
        <v-chip
          v-for="(item, i) in selectedFactors" 
          :key="i" 
          class="mr2 mb2 rounded-lg h36"
          label
          :class="chipClass(item.type)"
        >
          {{ item.title }}
        </v-chip>
        <div v-if="editable" class="w-100 mt3">
          <v-dialog
            v-model="dialogKey"
            scrollable
            persistent
            max-width="600px"
          >
            <template v-slot:activator="{ on, attrs }">
                <v-btn 
                  text
                  color="primary"
                  v-bind="attrs"
                  v-on="on"
                  class="rounded-lg"
                >
                  <v-icon left>
                    mdi-plus
                  </v-icon>elementy scenariusza
                </v-btn>
            </template>
            <v-card>
              <v-card-title>Czynniki z największym wpływem na scenariusz</v-card-title>
              <v-divider></v-divider>

              <v-card-text v-for="(item, i) in topicsFactorsTypes" :key="i"  :class="{mt3: i==0}">
                <h4 class="f6 mb-1 fw5">
                  {{ factorsTypes[item.id_factor_type].title }}
                </h4>
                <v-chip-group
                  v-if="item.id_factor_type == 1"
                  v-model="scenarioFactorsEdit"
                  column
                  multiple
                  :active-class="chipActiveClass(item.id_factor_type)"
                >
                  <v-chip
                    v-for="(item, i) in notKeyFactors" 
                    :key="i" 
                    filter
                    label
                    class="rounded-lg"
                    :value="item.id"
                  >
                    {{ item.title }}
                  </v-chip>
                </v-chip-group>
                <v-chip-group
                  v-else
                  v-model="scenarioFactorsEdit"
                  column
                  multiple
                  :active-class="chipActiveClass(item.id_factor_type)"
                >
                  <v-chip
                    v-for="(item, i) in factorsByType(item.id_factor_type)" 
                    :key="i" 
                    filter
                    label
                    class="rounded-lg"
                    :value="item.id"
                  >
                    {{ item.title }}
                  </v-chip>
                </v-chip-group>
              </v-card-text>

              <v-divider></v-divider>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  text
                  color="secondary"
                  @click="dialogKey = false"
                >
                  Anuluj
                </v-btn>
                <v-btn
                  text
                  color="primary"
                  @click="saveScenarioFactors"
                >
                  Zapisz
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

        </div>
      </div>
    </section>
    <section class="section-wrap center ph3 w-100 w-90-l">
      <div class="w-100 w-80-m w-75-l center mb6 mt6 f5">
        <editor
          v-if="editable"
          api-key="no-api-key"
          language="pl"
          cloud-channel="5"
          :disabled=false
          id="uuid"
          v-model="scenarioContent"
          :init= "{ menubar: false }"
          @onBlur="saveScenario"
          model-events=""
          placeholder="Wnioski, działania jakie należy podjąć w przypadku realizacji scenariusza"
          :inline=true
          plugins="lists link"
          tag-name="div"
          toolbar="undo redo | h1 h2 h3 | bold italic | alignleft aligncenter alignright alignjustify | bullist numlist | link "
          :value="scenarioContent"
          min_height="100"
          :inline_styles=false
        />
        <div v-else v-html="scenarioContent" class="fw5 f-www"></div>
      </div>
    </section>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from 'vuex';
import Editor from '@tinymce/tinymce-vue';
export default {
  props: ['id', 'nr'],
  components: {
    'editor': Editor
  },
  data: () => ({
    scenarioTitle: '',
    scenarioContent: '',
    scenarioFactors: [],
    axisLabelMin: 'Niskie oddziaływanie,<br />tendencja spadkowa',
    axisLabelMax: 'Wysokie oddziaływanie,<br />tendencja wzrostowa',
    dialogKey: false,
    scenarioFactorsEdit: [],
  }),
  computed: {
    ...mapGetters('scenarios', ['factorsTypes','topicsFactorsTypes', 'factorsByType', 'senarioFactors', 'steps', 'notKeyFactors', 'keyFactors', 'findFactorsMultiple', 'title', 'scenarioByNr', 'editable', 'loading']),
    scenario() {
      return this.scenarioByNr(this.nr)
    },
    factors() {
      return this.notKeyFactors
    },
    factorsLength() {
      return this.factors.length
    },
    keyFactorsLength() {
      return this.keyFactors.length
    },
    selectedFactors() {
      //return this.findFactorsMultiple(this.scenarioFactors)
      return this.senarioFactors(this.nr)
    },
    axisLegend() {
      if (this.nr == 1) {
        return { x: this.axisLabelMin, y: this.axisLabelMax }
      } else if (this.nr == 2) {
        return { x: this.axisLabelMax, y: this.axisLabelMax }
      } else if (this.nr == 3) {
        return { x: this.axisLabelMin, y: this.axisLabelMin }
      } else {
        return { x: this.axisLabelMax, y: this.axisLabelMin }
      }
    },
    scenarioFactorsIds() {
      return this.selectedFactors.map( el => el.id )
    }
  },
  created() {
    this.scenarioTitle = this.scenario.title;
    this.scenarioContent = this.scenario.content;
    this.scenarioFactors = this.scenarioFactorsEdit = this.scenarioFactorsIds;
    this.setCurrentSlide(2);
  },
  methods: {
    ...mapActions('scenarios', ['updateScenario','updateScenarioFactors']),
    ...mapMutations('scenarios', ['setCurrentSlide']),
    saveScenario() {
      this.updateScenario({
        id_topic: this.id,
        id: (this.scenario.id) ? this.scenario.id : '',
        ord: this.nr,
        title: this.scenarioTitle,
        content: this.scenarioContent
      })
      //factors: JSON.stringify(this.scenarioFactors)
      //.then(() => this.$router.go(-1))
    },
    saveScenarioFactors() {
      this.scenarioFactors = this.scenarioFactorsEdit;
      this.updateScenarioFactors({
        id_topic: this.id,
        nr: this.nr,
        factors: JSON.stringify(this.scenarioFactors)
      })
      .then(() => this.dialogKey = false)
    },
    chipClass(type) {
      return {
        'chip1': type == 1,
        'chip2': type == 2,
        'chip3': type == 3,
        'chip4': type == 4,
        'chip5': type == 5,
        'chip6': type == 6,
        'chip-custom': type > 6
      }
    },
    chipActiveClass(type) {
      return (type < 7) ? 'chip'+type : 'chip-custom'
    }
  }
}
//https://www.martyfriedel.com/blog/integrating-tiny-with-vue-in-a-real-world-application
</script>

<style>
.discrete {
  outline: none;
}
.discrete::placeholder, .mce-content-body[data-mce-placeholder]:not(.mce-visualblocks)::before {
  color: #C9C9C9 !important;
}
.mw-third {
  max-width: 40%;
}
.mce-content-body {
  min-height: 100px;
}
</style>