<template>
  <div v-if="editable" class="w-100 overflow-hiddenx bg-light">
    <div class="top-space"></div>
    <div class="relative">
      <div class="absolute z-2 slider-prev">
        <v-btn
          icon
          color="primary"
          x-large
          height="72"
          width="72"
          title="Wróć do poprzedniego kroku"
          @click="prevSlide"
          :disabled="sliderModel == 0"
          class="absolute"
        >
          <v-icon size="42">mdi-arrow-left</v-icon>
        </v-btn>
      </div>
      <div class="absolute z-2 slider-next">
        <v-btn
          v-if="sliderModel != 2"
          icon
          color="primary"
          x-large
          height="72"
          width="72"
          title="Następny krok"
          @click="nextSlide"
          :disabled="sliderModel == 1 || keyFactorsLength < 2 || loading"
        >
          <v-icon size="42">mdi-arrow-right</v-icon>
        </v-btn>
      </div>

      <div class="section-wrap center ph3 w-80 w-70-l relative">
        <div class="absolute slider-i">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 66.874 60.422" height="200.9623" width="222.421"><path d="M50.155 59.168c-2.896 1.672-30.54 1.672-33.437 0C13.823 57.496 0 33.555 0 30.211 0 26.867 13.823 2.926 16.718 1.254c2.896-1.672 30.541-1.672 33.437 0 2.896 1.672 16.719 25.613 16.719 28.957 0 3.344-13.823 27.285-16.719 28.957z" opacity=".449" fill="#5edbeb"/></svg>
        </div>
      </div>

      <v-carousel v-model="sliderModel" height="160" light :show-arrows="false" touchless hide-delimiters>
        <v-carousel-item
          v-for="(step, i) in steps"
          :key="i"
        >
          <section class="w-100 relative items-center h-100 ">
            <div class="flex h-100 items-center section-wrap center ph3 w-80 w-70-l relative">
              
              <div class="c-dark flex-grow-1 f-www">
                <div class="f3 fw7 mb2">{{ step.title }}</div>
                <div>{{ step.content }}</div>
              </div>
            </div>
          </section>
        </v-carousel-item>
      </v-carousel>
    </div>
  </div>
  <div v-else class="top-space-half"></div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
export default {
  data: () => ({
    sliderModel: 0
  }),
  computed: {
    ...mapGetters('scenarios', ['id','title','steps','currentSlide','possibleStep','editable','keyFactorsLength','loading']),
  },
  watch: {
    currentSlide(slide) {
      if (slide != this.sliderModel) {
        this.sliderModel = slide
      }
    }
  },
  methods: {
    ...mapMutations('scenarios', ['setCurrentSlide']),
    prevSlide() {
      let prevSlide = this.currentSlide - 1;
      this.setCurrentSlide(prevSlide);
      
      if (prevSlide == 0) {
        this.$router.push('/tematy/' + this.id);
      } else if (prevSlide == 1) {
        this.$router.push('/tematy/' + this.id + '/scenariusze');
      }
    },
    nextSlide() {
      if (this.currentSlide == 0) {
        this.$router.push('/tematy/' + this.id + '/scenariusze');
      }
      
      /*let nextSlide = this.currentSlide + 1;
      this.setCurrentSlide(this.nextSlide);
      
      if (nextSlide == 1) {
        this.$router.push('/tematy/' + this.id + '/scenariusze');
      } */
    }
  }
}
</script>