<template>
  <div class="relative btc tc" @mouseover="hiddenDelete=false" @mouseleave="hiddenDelete=true">
    <template v-if="topicFactorType.id">
      <div v-if="!factorsLenght > 0 || (nr == 0 && factorsLenght < 2)" class="pulse-bg" :style="{ backgroundColor: factorsTypes[topicFactorType.id_factor_type].color }"></div>
      <v-tooltip top max-width="400" 
        :color="factorsTypes[topicFactorType.id_factor_type].color"
        :disabled="!factorsTypes[topicFactorType.id_factor_type].content">
        <template v-slot:activator="{ on, attrs }">
          <v-hover v-slot="{ hover }">
            <v-btn block height="64" 
              :elevation="hover ? 8 : 0"
              :color="factorsTypes[topicFactorType.id_factor_type].color"
              v-bind="attrs"
              v-on="on"
              @click.stop="(editable) ? $emit('add-factor', topicFactorType.id_factor_type) : null"
              class="white--text rounded-lg z-3"
            >
              {{ (editable) ? '+ ' : '' }}{{ factorsTypes[topicFactorType.id_factor_type].title }}
            </v-btn>
          </v-hover>
        </template>
        <span v-text="factorsTypes[topicFactorType.id_factor_type].content"></span>
      </v-tooltip>
      <v-fab-transition>
        <v-btn
          v-if="nr > 0 && editable"
          v-show="!hiddenDelete"
          color="#db2f89"
          fab
          dark
          x-small
          absolute
          bottom
          left
          @click.stop="$emit('del-type', topicFactorType)"
        >
          <v-icon>mdi-minus</v-icon>
        </v-btn>
      </v-fab-transition>
    </template>
    <template v-else-if="topicsFactorsTypes[nr-1]">
      <div v-if="keyFactorsLength == 2 && !topicFactorType.id" class="pulse-ico mr2"></div>
      <v-menu
        top
        dark
        rounded="lg"
        >
        <template v-slot:activator="{ on: menu, attrs }">
          <v-tooltip bottom>
            <template v-slot:activator="{ on: tooltip }">
              <v-btn
              :outlined="keyFactorsLength < 2"
              :disabled="keyFactorsLength < 2"
              depressed
                fab
                color="#c9c9c9"
                v-bind="attrs"
                v-on="{ ...tooltip, ...menu }"
                class="mr2 z-4"
                :dark="keyFactorsLength == 2"
              >
              <v-icon>mdi-plus</v-icon>
              </v-btn>
            </template>
            <span>Dodaj inne elementy</span>
          </v-tooltip>
        </template>
        <v-list class="pa0">
          <v-list-item
            v-for="(item, index) in availableTypes"
            :key="index"
            @click="addType(item.id)"
            :style="{ 'background-color': item.color }"
          >
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item>
          <v-list-item
            :style="{ 'background-color': '#18b9a7' }"
            @click="$emit('create-new-type')"
          >
            <v-list-item-title>Inne</v-list-item-title>
            <v-list-item-icon>
              <v-icon>mdi-plus</v-icon>
            </v-list-item-icon>
          </v-list-item>
        </v-list>
      </v-menu>
    </template>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  name: 'DiagramButton',
  props: ['topicsFactorsType', 'nr'],
  data: () => ({
    hiddenDelete: true,
  }),
  computed: {
    ...mapGetters('scenarios', ['id','topicsFactorsTypes','topicsFactorsTypesLength','factorsByType','factorsByTypeLength','keyFactorsLength','factorsTypes', 'keyFactors', 'editable']),
    usedTypes() {
      return (this.topicsFactorsTypes) ? this.topicsFactorsTypes.map((e) => parseInt(e.id_factor_type) ) : []
    },
    availableTypes() {      
      if (Object.keys(this.factorsTypes).length > 0) {
        let x = Object.keys(this.factorsTypes).filter((key) => !this.usedTypes.includes(parseInt(key))).map((key) => ({[key]: this.factorsTypes[key]}));
        return (x.length > 0) ? Object.assign(...x) : []
      } else {
        return []
      }
      //return (Object.keys(this.factorsTypes).length > 0) ? Object.assign(...Object.keys(this.factorsTypes).filter((key) => !this.usedTypes.includes(parseInt(key))).map((key) => ({[key]: this.factorsTypes[key]}))) : [];
    },
    topicFactorType() {
      return (this.topicsFactorsTypes[this.nr]) ?  this.topicsFactorsTypes[this.nr] : {}
    },
    factorsLenght() {
      return this.factorsByTypeLength(this.topicFactorType.id_factor_type)
    }
  },
  methods: {
    ...mapActions('scenarios', ['addTopicType']),
    addType(id) {
      this.addTopicType({
        id_topic: this.id,
        id_factor_type: id
      });
    },
  }
}
</script>